import React from "react";
import computer from "../Icons/computer.png";
import backend from "../Icons/backend.png";
import api from "../Icons/api.png";
import algo from "../Icons/algo.png";
import puzzle from "../Icons/puzzle.jpg";
import Skillcard from "../Components/Skillcard";
import {motion} from "framer-motion";
const skills=[
    {
        icon:computer,
        title:"Frontened Development",
        about:"I can build a beautiful website using React Js"
    },
    {
        icon:backend,
        title:"Backened Developers",
        about:"I can handle Post gre sql, Graph QL, API using React Js"
    },
    {
        icon:api,
        title:"API Development",
        about:"I can develop robust REST API using React Js and Node Js"
    },
    {
        icon:algo,
        title:"Competituve coding",
        about:"A daily problem solver in hacker and hackerearth using python"
    },
    {
        icon:puzzle,
        title:"UI/UX designer",
        about:"I created a portfolio website using React Js"
    }
]
const About=()=>
{
    const about_variants=
    {
        hidden:
        {
            opacity:0
        },
        visible:
        {
            opacity:1,
            transition:
            {
                delay:0.1,
                duration:0.6
            }
        },
        exit:
        {
            opacity:0,
            transition:
            {
                ease:"easeInOut"
            }
        }
    }
    return(
        <motion.div className="about"
            variants={about_variants}
            initial="hidden"
            animate="visible"
            exit="exit"
        >
            <h6 className="about_intro">
                This is Srikanth from Guntur, Andhra pradesh. I am a quick learner. But I am beginner of the React JS.
            </h6>
            <div className="container about_container">
                <h6 className="about_heading">What I offer</h6>
                <div className="row">
                    {
                        skills.map(skill=>
                            <Skillcard skill={skill}/> 
                        )
                    }
                </div>
            </div>
        </motion.div>
    )
}
export default About