import React from "react";
import facebook from "../Icons/facebook.png";
import instagram from "../Icons/instagram-icon.jpg";
import pin from "../Icons/pin.png";
import tie from "../Icons/tie.jpg";
import kranthi from "../projects_images/kranthi.jpg";
import resume from "../projects_images/resume.pdf";
import {motion} from "framer-motion";
const Sidebar=()=>
{
    const handleEmailMe=() =>
    {
        window.open("mailto:devisettysrikanth@gmail.com"); 
    }
    const sidebar_variant=
    {
        hidden:
        {
            x:"-20vw"
        },
        visible:
        {
            x:0,
            transition:
            {
                delay:0.1,
                duration:0.5,
                type:"spring"
            }
        }
    }
    return (
      <motion.div className="sidebar"
        variants={sidebar_variant}
        initial="hidden"
        animate="visible"
      >
        <img src={kranthi} alt="avatar" className="sidebar_avatar"/>
        <div className="sidebar_name">DEVISETTY <span>SRIKANTH</span></div>
        <div className="sidebar_item sidebar_title">Web Developer</div>
        <a href={resume} download="resume.pdf">
          <div className="sidebar_item sidebar_resume">
              <img src={tie} alt="resume" className="sidebar_icon"/>Download Resume
          </div>
        </a>
        <figure className="sidebar_social-icons my-2">
          <a href="https://www.facebook.com/idskranthi/"><img src={facebook} alt="facebook" className="sidebar_icon mr-3"/></a>
          <a href="https://www.instagram.com/kranthids/"><img src={instagram} alt="instagram" className="sidebar_icon"/></a>
        </figure>
        <div className="sidebar_contact">
          <div className="sidebar_item">
              <img src={pin} alt="location" className="sidebar_icon mr-3"/>
              Chennai, India</div>
          <div className="sidebar_item">devisettysrikanth@gmail.com</div>
          <div className="sidebar_item">9985091805/9080803970</div>
        </div>
        <div className="sidebar_item sidebar_email" onClick={handleEmailMe}>Email me</div>
      </motion.div>
    );
}
export default Sidebar;