import React from "react";
import react from "../Icons/reactjs.png";
import python from "../Icons/python.png";
import html5 from "../Icons/HTML5.png";
import Visualstudiocode from "../Icons/Visualstudiocode.jpg";
import bootstrap from "../Icons/bootstrap.jpg";
import framer from "../Icons/framer.jpg";
import nodejs from "../Icons/nodejs.png";
import Bar from "./Bar";
import {motion} from "framer-motion";
const Resume=()=>
{
    const languages=
[
    {
        icon:python,
        name:"Python",
        level:"45"
    },
    {
        icon:react,
        name:"React JS",
        level:"80"
    },
    {
        icon:html5,
        name:"HTML",
        level:"60"
    },
    {
        icon:bootstrap,
        name:"Bootstrap",
        level:"75"
    }
];
const Tools=
[
    {
        icon:Visualstudiocode,
        name:"Visual Studio Code",
        level:"70"
    },
    {
        icon:framer,
        name:"Framer",
        level:"65"
    },
    {
        icon:nodejs,
        name:"Node Js",
        level:"60"
    },
];
    const resume_variants=
    {
        hidden:
        {
            opacity:0
        },
        visible:
        {
            opacity:1,
            transition:
            {
                delay:0.1,
                duration:0.6
            }
        },
        exit:
        {
            opocity:0,
            transition:
            {
                ease:"easeInOut"
            }
        }
    }
    return(
        <motion.div className="container resume"
            variants={resume_variants}
            initial="hidden"
            animate="visible"
            exit="exit"
        >
            <div className="row">
                <div className="col-lg-6 resume-card">
                    <h4 className="resume-card_heading">
                        Education
                    </h4>
                    <div className="resume-card_body">
                        <h5 className="resume-card_title">
                            Computer Science Engineering
                        </h5>
                        <p className="resume-card_name">
                            Bachelor of Technology
                        </p>
                        <p className="resume-card_details">
                            I am currently pursing B.TECH Computer Science and Engineering 4th year in SRM Institue of Science and Engineering.
                        </p>
                    </div>
                </div>
                <div className="col-lg-6 resume card">
                    <h4 className="resume-card_heading">
                        Experience
                    </h4>
                    <div className="resume-card_body">
                        <h5 className="resume-card_title">
                            Intern
                        </h5>
                        <p className="resume-card_name">
                            Kaashiv InfoTech(2020)
                        </p>
                        <p className="resume-card_details">
                            I worked as a Intern in Kaashiv InfoTech and also completed one project on web development. 
                        </p>
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col-lg-6 resume-languages">
                    <h5 className="resume-language_heading">
                        Language and Framework
                    </h5>
                    <div className="resume-language_body mt-3">
                    {
                        languages.map(Language=>
                            <Bar value={Language}/>
                            )
                    }
                    </div>
                </div>
                <div className="col-lg-6 resume-tools">
                    <h5 className="resume-tool_heading">
                        Tools and Softwares
                    </h5>
                    <div className="resume-tool_body mt-3">
                    {
                        Tools.map(tool=>
                        <Bar value={tool}/>
                        )
                    }
                    </div>
                </div>
            </div>
        </motion.div>
    );
};
export default Resume;